import React from 'react';
const SobreMi = () => {

      var tags = [
            {
                  name :' api'
            },
            {
                  name :' api rest'
            },
            {
                  name :' api'
            }
      ]

      
    return (  
      <section className="about" id="SobreMi">
      <h2>Sobre Mi</h2>
      <div className="divider"></div>
      <div className="container">
            <div className="row row-cols-sm-3 row-cols-md-3 about-content justify-content-center"  data-aos="fade-up" data-aos-duration="2000">
    <SobreMiInfo 
      titulo="Acerca de" 
//  
parrafo1="Soy Front-end Developer con 4 años de experiencia en aplicaciones web ,sitios web y portales web,  interesando el desarrollo web morderno y las
nuevas tecnologías de la actualidad."
      />
      <SobreMiInfo 
      titulo="Educación" 
      parrafo="Universidad Nacional Experimental Politécnica de la Fuerza Armada
Ingeniero De Sistemas 2018 - 2025"
parrafo1="Gustavo H. Machado 2013 - 2018
Bachillerato"

      />
       <SobreMiInfo 
      titulo="Experiencia laboral" 
      parrafo3="Gsoxware TI (1 año)
      Desarrollador junior"
      parrafo="Fogade  (1 año)
Asistente Informático"
parrafo1="Consejo Nacional Electoral  (9 meses)
Programador de software "
parrafo2="Canaima Distribución  (3 meses)
Operador / Almacenista"

      />
     
     
     </div>
    </div>
    <h2>TAGS</h2>
      <div className="divider"></div>
<span >

      <span className="row">
      {tags.map(a =>

    <div className=" col-md-1 m-1 about-info " >
      <span> {a.name}</span>
      
    </div>

)

}
</span>
</span>
    
  </section> );
}
 
export default SobreMi;


const SobreMiInfo = ({titulo,parrafo3,parrafo,parrafo1,parrafo2})=>{
    return(  
      <div className=" col-md-3 m-3 about-info" >
      <h3>{titulo}</h3>
      { parrafo3 && 
       <p>{parrafo3}</p>
       
      }
      <br/>
        <p>{parrafo}</p>
        <p>{parrafo1}</p>
        <br/>
        <p>{parrafo2}</p>
      </div>
      )
}

