import React from 'react';
import images from '../assets/images/propuesta.png'
import images1 from '../assets/images/chart.jpg'
import images2 from '../assets/images/remesas.jpg'

const Propuestas = () => {
    return ( <section className="about"  id="Propuestas">
    <h2>Propuestas</h2>
    <div className="divider"></div>
    <div className="about-content">
   <div className="container">
   	<div className="row justify-content-center">
     <div className="col-auto  mt-2" 
     data-aos="fade-right"
     data-aos-offset="500"
     data-aos-easing="ease-in-sine">
     <a href="https://ruizsergio.github.io/Chartjs/" >
    <div className="about-info" style={{width:'350px',height:'300px'}}>
    <img src={images1} style={{width:'100%',height:'200px',borderRadius:"15px"}} alt="..."/>
   <p className="p" >Angular Chartjs</p>
     
    </div>
    </a>
    </div>
     

    <div className="col-auto mt-2" 
    data-aos="fade-down"
     data-aos-easing="linear"
     data-aos-duration="1500">
    <a href="https://ruizsergio.github.io/Remesas/" >
    <div className="about-info" style={{width:'350px',height:'300px'}}>
    <img src={images2} style={{width:'100%',height:'200px',borderRadius:"15px"}} alt="..." />
   <p className="p" >Vue Formulario</p>
     
    </div>
    </a>
    </div>
    

    <div className="col-auto mt-2" 
    data-aos="fade-left"
    data-aos-offset="500"
     data-aos-duration="500">
    <a href="https://ruizsergio.github.io/bdv/" >
    <div className="about-info" style={{width:'350px',height:'300px'}}>
  <img src={images} style={{width:'100%',height:'200px',borderRadius:"15px"}} alt="..."/>
   <p className="p" >React Dashboard</p>
     
    </div>
    </a>
    </div>
    



    
    </div>
   </div>
     </div>
   
    
  </section>
  );
}
 
export default Propuestas;