import React, { useEffect }  from 'react';
import SmoothScroll from "smooth-scroll"




const Header = () => {
  useEffect(() => {
    SmoothScroll('a[href*="#"]', {
      speed: 1000,
      speedAsDuration: true
    })
  }, []);                     
    return ( 
        <header className="header">
        <nav className="navbar">
         <div className="container">
   			<ul className="row justify-content-center" >
            <li className="col-auto p-1 m-3" ><a  href="#">Inicio</a></li>
            <li className="col-auto p-1 m-3" ><a href="#SobreMi">Sobre Mi</a></li>
            <li className="col-auto p-1 m-3" ><a href="#Tecnologia">Tecnologia</a></li>
            <li className="col-auto p-1 m-3" ><a href="#Propuestas">Propuestas</a></li>
        </ul>
        </div>
        </nav>
      </header>
     );
}
 
export default Header;